import React, { ChangeEvent, FC, FormEvent, useRef, useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useDispatch, useSelector } from '../../services/store';

import { resetPassword } from '../../services/slices/password/password';

import styles from '../form.module.scss';
import { Button, Input } from '@ya.praktikum/react-developer-burger-ui-components';

export const ResetPassword: FC = () => {
  const dispatch = useDispatch();
  const inputRef = useRef<HTMLInputElement>(null);
  const [isPasswordVisible, setIsPasswordVisible] = useState(false);
  const [formData, setFormData] = useState({
    password: '',
    token: '',
  });
  const { isLoading, isPasswordReset } = useSelector((state) => state.password);

  const onChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    const name = e.target.name;

    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const onIconClick = () => {
    if (inputRef && inputRef.current) {
      inputRef.current.type = inputRef.current.type === 'password' ? 'text' : 'password';
      setIsPasswordVisible((prevState) => !prevState);
    }
  };

  const onSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(resetPassword(formData));
  };

  if (isPasswordReset) return <Redirect to="/login" />;

  return (
    <section className={styles.formWrapper}>
      <form className={styles.form} onSubmit={onSubmit}>
        <h2 className={styles.formTitle}>ÐÐ¾ÑÑÑÐ°Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ðµ Ð¿Ð°ÑÐ¾Ð»Ñ</h2>

        <div className={styles.inputWrapper}>
          <Input
            ref={inputRef}
            type={'password'}
            placeholder={'ÐÐ²ÐµÐ´Ð¸ÑÐµ Ð½Ð¾Ð²ÑÐ¹ Ð¿Ð°ÑÐ¾Ð»Ñ'}
            icon={isPasswordVisible ? 'HideIcon' : 'ShowIcon'}
            onIconClick={onIconClick}
            onChange={onChange}
            value={formData.password}
            name={'password'}
          />
        </div>
        <div className={styles.inputWrapper}>
          <Input
            type={'text'}
            placeholder={'ÐÐ²ÐµÐ´Ð¸ÑÐµ ÐºÐ¾Ð´ Ð¸Ð· Ð¿Ð¸ÑÑÐ¼Ð°'}
            onChange={onChange}
            value={formData.token}
            name={'token'}
          />
        </div>

        {isLoading ? (
          <span className="text text_type_main-default">ÐÐ´ÑÑ Ð·Ð°Ð¿ÑÐ¾Ñ...</span>
        ) : (
          <Button type="primary" size="medium">
            ÐÐ¾ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ
          </Button>
        )}
      </form>

      <div className={styles.flexWrapper}>
        <span className={styles.formText}>ÐÑÐ¿Ð¾Ð¼Ð½Ð¸Ð»Ð¸ Ð¿Ð°ÑÐ¾Ð»Ñ?</span>
        <Link className={styles.formLink} to="/login">
          ÐÐ¾Ð¹ÑÐ¸
        </Link>
      </div>
    </section>
  );
};
