import React, { ChangeEvent, FormEvent, useState } from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from '../../services/store';

import { loginRequest } from '../../services/slices/auth/auth';

import styles from '../form.module.scss';
import { Button, Input, PasswordInput } from '@ya.praktikum/react-developer-burger-ui-components';

export const Login = () => {
  const dispatch = useDispatch();
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });
  const { isLoading } = useSelector((state) => state.auth);

  const onChange = (e: ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    const name = e.target.name;

    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const onSubmit = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    dispatch(loginRequest(formData));
  };

  return (
    <section className={styles.formWrapper}>
      <form className={styles.form} onSubmit={onSubmit}>
        <h2 className={styles.formTitle}>ÐÑÐ¾Ð´</h2>

        <div className={styles.inputWrapper}>
          <Input
            type={'email'}
            placeholder={'Email'}
            onChange={onChange}
            value={formData.email}
            name={'email'}
          />
        </div>
        <div className={styles.inputWrapper}>
          <PasswordInput onChange={onChange} value={formData.password} name={'password'} />
        </div>

        {isLoading ? (
          <span className="text text_type_main-default">ÐÐ´ÑÑ Ð·Ð°Ð¿ÑÐ¾Ñ...</span>
        ) : (
          <Button type="primary" size="medium">
            ÐÐ¾Ð¹ÑÐ¸
          </Button>
        )}
      </form>

      <div className={`${styles.flexWrapper}  mb-4`}>
        <span className={styles.formText}>ÐÑ â Ð½Ð¾Ð²ÑÐ¹ Ð¿Ð¾Ð»ÑÐ·Ð¾Ð²Ð°ÑÐµÐ»Ñ?</span>
        <Link className={styles.formLink} to="/register">
          ÐÐ°ÑÐµÐ³Ð¸ÑÑÑÐ¸ÑÐ¾Ð²Ð°ÑÑÑÑ
        </Link>
      </div>
      <div className={styles.flexWrapper}>
        <span className={styles.formText}>ÐÐ°Ð±ÑÐ»Ð¸ Ð¿Ð°ÑÐ¾Ð»Ñ?</span>
        <Link className={styles.formLink} to="/forgot-password">
          ÐÐ¾ÑÑÑÐ°Ð½Ð¾Ð²Ð¸ÑÑ Ð¿Ð°ÑÐ¾Ð»Ñ
        </Link>
      </div>
    </section>
  );
};
