      <div className={styles.totalWrapper}>
        <span className={styles.totalTitle}>ÐÑÐ¿Ð¾Ð»Ð½ÐµÐ½Ð¾ Ð·Ð° ÑÐµÐ³Ð¾Ð´Ð½Ñ:</span>
        <span className={`${styles.digits} ${styles.digitsLarge}`}>{totalToday}</span>
      </div>
